import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '@environments/environment';
import { Country } from '@models/coutries/country';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CountriesService {

  constructor(private http: HttpClient) { }

  getCountries(): Observable<Country[]> {
    return this.http.get<Country[]>(environment.api_prefix + 'country');
  }

  addCountry(country: Country): Observable<Country> {
    return this.http.post<Country>(environment.api_prefix + 'country', country);
  }

  updateCountry(country: Country): Observable<Country> {
    return this.http.put<Country>(environment.api_prefix + `country/${country.id}`, country)
  }

  deleteCountry(country: Country): Observable<Country> {
    return this.http.delete<Country>(environment.api_prefix + `country/${country.id}`);
  } 
}
