import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ClientCard } from '@models/clients/client-card';
import { HttpClient } from '@angular/common/http';
import { environment } from '@environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ClientsService {

  constructor(private http: HttpClient) { }

  getClients(): Observable<ClientCard[]> {
    return this.http.get<ClientCard[]>(environment.api_prefix + 'clients');
  }

  addClient(client: ClientCard): Observable<ClientCard> {
    return this.http.post<ClientCard>(environment.api_prefix + 'clients', client);
  }

  editClient(client: ClientCard): Observable<ClientCard> {
    return this.http.put<ClientCard>(environment.api_prefix + `clients/${client.id}`, client)
  }

  deleteClient(client: ClientCard): Observable<ClientCard> {
    return this.http.delete<ClientCard>(environment.api_prefix + `clients/${client.id}`);
  } 
}
